* {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}

.custom__card {
    background: linear-gradient(180deg, #FF6F48 0%, #F02AA6 100%);
    border-radius: 15px;
    padding: 3rem 2rem 5rem 2rem;
    text-align: left;
    color: #FFF;
    font-weight: 800;
    font-size: 32px;
}

@media screen and (max-width: 956px) {
    .custom__card {
        font-size: 24px;
        line-height: 32px;
    }
}

@media screen and (max-width: 630px) {
    .custom__card {
        padding: 2rem;
        max-height: 200px;
    }
}