* {
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
    padding: 0;
    margin: 0;
}

body {
    background:-moz-linear-gradient(180deg, #FFFFFF 0%, #83869A 100%); 
    background:-webkit-linear-gradient(180deg, #FFFFFF 0%, #83869A 100%);
    background:-o-linear-gradient(180deg, #FFFFFF 0%, #83869A 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#83869A', endColorstr='#FFFFFF', GradientType=1 );
    background:-ms-linear-gradient(180deg, #FFFFFF 0%, #83869A 100%);
    background:linear-gradient(180deg, #FFFFFF 0%, #83869A 100%);

}

button {
    cursor: pointer;
}

.app {
    overflow-x: hidden;
    font-family: 'Plus Jakarta Sans', sans-serif;
    max-width: 100vw;
    min-height: 100vh;
    padding: 2rem 10vw 0 10vw;
}

.app__navbar {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.app__navbar p {
    font-weight: 800;
    font-size: 32px;
}

.app__navbar button {
    z-index: 2;
    border: none;
    border-radius: 28px;
    padding: .7em 1.2em;
    background: #13183F;
    color: #FFFFFF;
    font-family: 'Plus Jakarta Sans', sans-serif;
    font-size: 18px;
    font-weight: 700;;
}

.app__navbar button:focus {
    background: #666CA3;
}

.app__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.app__header-text {
    display: flex;
    flex-direction: column;
    width: 50%;
}

.app__header-image {
    width: 100%;
    height: auto;
}

.app__header-image img {
    position: relative;
    top: -5rem;
    right: -10vw;
    width: 100%;
    height: auto;
}

.app__header-text h1 {
    inline-size: 550px;
    overflow-wrap: break-word;
    font-weight: 800;
    font-size: 56px;
    line-height: 71px;
    color: #13183F;
}

.app__header-text p {
    inline-size: 440px;
    overflow-wrap: break-word;
    font-weight: 500;
    font-size: 18px;
    line-height: 28px;
    color: #83869A;
    margin: 2rem 0;
}

.app__header-text button {
    background: linear-gradient(180deg, #FF6F48 0%, #F02AA6 100%);
    border-radius: 31.5px;
    font-family: "Plus Jakarta Sans", sans-serif;
    font-weight: 700;
    font-size: 18px;
    line-height: 28px;
    color: #FFF;
    border: none;
    padding: 1rem .5rem;
    width: 150px;
    margin-bottom: 2rem;
}

.app__header-text button:focus {
    background: linear-gradient(0deg, rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0.5)), linear-gradient(180deg, #FF6F48 0%, #F02AA6 100%);
}

.app__cards {
    display: grid;
    grid-template-columns: repeat(3, minmax(0, 1fr));
    grid-template-rows: repeat(2, minmax(0, 1fr));
    grid-gap: 6rem 2rem;
    margin-bottom: 10rem;
}

.app__footer {
    margin: 0 -10vw;
    padding: 2rem 10vw;
    background: #13183F;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.app__footer p {
    color: #FFF;
    font-size: 32px;
    font-weight: 800;
}

.app__footer button {
    border: none;
    border-radius: 28px;
    padding: .7em 1.2em;
    background: linear-gradient(180deg, #4851FF -54.32%, #F02AA6 100%);
    color: #FFFFFF;
    font-family: 'Plus Jakarta Sans', sans-serif;
    font-size: 18px;
    font-weight: 700;;
}

.app__footer button:focus {
    background: linear-gradient(0deg, rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0.5)), linear-gradient(180deg, #4851FF -54.32%, #F02AA6 100%);
}

@media screen and (max-width: 956px) {
    .app {
        padding: 1rem;
    }
    .app__navbar button {
        font-size: 16px;
    }
    .app__header {
        margin-top: 2rem;
    }

    .app__header-text {
        width: 100%;
    }

    .app__header-text h1 {
        font-size: 40px;
        line-height: 50px;
    }
    .app__header-text p {
        font-size: 16px;
        line-height: 26px;
    }
    .app__header-text button {
        font-size: 16px;
        line-height: 26px;
    }
    .app__header-image img {
        position: relative;
        top: -5rem;
        right: -1rem;
    }
    .app__cards {
        grid-template-columns: repeat(2, minmax(0, 1fr));
    }
    .app__footer button {
        font-size: 16px;
        line-height: 28px;
    }
    .app__header-text h1,
    .app__header-text p {
        inline-size: auto;
    }
    .app__header {
        margin-bottom: 2rem;
    }
}

@media screen and (max-width: 630px) {
    .app {
        padding: 1rem .5rem;
    }
    .app__header {
        flex-direction: column;
    }
    .app__header-text {
        width: 100%;
    }
    .app__header-image img { 
        position: initial;
        width: 100%;
    }
    .app__cards {
        grid-template-columns: minmax(230px, 1fr);
        grid-template-rows: 150px repeat(5, minmax(0, 1fr));
        grid-gap: 2rem;
    }
    .app__header-text h1,
    .app__header-text p {
        inline-size: auto;
    }
}