.card {
    padding: 0 2rem;
    background: #FFF;
    box-shadow: 0px 25px 50px rgba(6, 22, 141, 0.0442381);
    border-radius: 15px;
}

.card__icon {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: -23px;
    width: 56px;
    height: 56px;
    border-radius: 50%;
    background: linear-gradient(180deg, #FF6F48 0%, #F02AA6 100%);
}

.card__info {
    min-height: 15rem;
}

.card__info h2 {
    color: #13183F;
    font-weight: 800;
    font-size: 24px;
    line-height: 30px;
    margin: 1rem 0;
}

.card__info p {
    color: #83869A;
    font-weight: 500;
    font-size: 18px;
    line-height: 28px;
}

.card button {
    margin: 2rem 0;
    cursor: pointer;
    border: none;
    background: #FFF;
    color: #F74780;
    font-family: "Plus Jakarta Sans", sans-serif;
    font-weight: 700;
    font-size: 18px;
    line-height: 28px;
}

.card button:focus {
    color: #FFA7C3;
}

@media screen and (max-width: 956px) {
    .card__info h2 {
        font-size: 20px;
        line-height: 25px;
    }
    .card__info p {
        font-size: 16px;
        line-height: 26px;
    }
    .card__info {
        min-height: 0;
    }
}